<template>
    <header>
      <div class="container">
        <h1><a href="#"><img src="img/image.png/image.png" alt=""></a></h1>
        <ul>
          <router-link to="/"><li><a href="">Trang Chủ</a></li></router-link>
          <li><a href="#">Giỏ Hàng</a></li>
        </ul>
      </div>
    </header>
    
    <div class="container">
      <div class="cart">
        <h2>Giỏ Hàng Của Bạn</h2>
        <div id="cart-items">
          <!-- Sản phẩm trong giỏ hàng sẽ hiển thị tại đây -->
        </div>
        <div id="empty-message">
          <p>Hiện tại chưa có sản phẩm nào trong giỏ hàng.</p>
        </div>
      </div>
    </div>  
  </template>
  
  <script setup>
  import { ref, onMounted } from 'vue';
  
  const cartItemsContainer = ref(null);
  const emptyMessage = ref(null);
  
  // Hàm hiển thị giỏ hàng
  function displayCartItems(cart) {
    cartItemsContainer.value.innerHTML = ''; // Xóa hết sản phẩm cũ
    if (cart.length > 0) {
      emptyMessage.value.style.display = 'none';
      // Hiển thị các sản phẩm trong giỏ hàng
      cart.forEach(product => {
        const productElement = document.createElement('div');
        productElement.classList.add('cart-items');
        productElement.innerHTML = `
          <img src="${product.image}" alt="${product.title}" width="100">
          <div class="product-info">
            <h3>${product.title}</h3>
            <p>Số lượng: ${product.quantity}</p>
            <p>Giá: ${product.price} VND</p>
            <button class="remove-btn" data-id="${product.id}">Xóa 1</button>
          </div>
        `;
        cartItemsContainer.value.appendChild(productElement);
      });
  
      // Thêm sự kiện xóa từng sản phẩm khỏi giỏ hàng
      document.querySelectorAll('.remove-btn').forEach(button => {
        button.addEventListener('click', function() {
          const productId = parseInt(this.getAttribute('data-id'));
          removeOneFromCart(productId, cart);
        });
      });
    } else {
      emptyMessage.value.style.display = 'block';
    }
  }
  
  // Hàm giảm số lượng hoặc xóa sản phẩm khỏi giỏ hàng
  function removeOneFromCart(productId, cart) {
    const productIndex = cart.findIndex(product => product.id === productId);
    if (productIndex !== -1) {
      if (cart[productIndex].quantity > 1) {
        cart[productIndex].quantity--;
      } else {
        cart.splice(productIndex, 1); // Xóa sản phẩm khỏi giỏ hàng
      }
      localStorage.setItem('cart', JSON.stringify(cart));
      displayCartItems(cart); // Cập nhật giao diện giỏ hàng ngay lập tức
    }
  }
  
  onMounted(() => {
    cartItemsContainer.value = document.getElementById('cart-items');
    emptyMessage.value = document.getElementById('empty-message');
  
    // Lấy giỏ hàng từ localStorage
    let storedCart = localStorage.getItem('cart');
    let cart = storedCart ? JSON.parse(storedCart) : [];
  
    displayCartItems(cart);
  });
  </script>
  
  <style scoped>
  body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
  }
  .container {
    width: 100%;
    margin: auto;
    overflow: hidden;
  }
  header ul {
    background-color: rgb(19, 250, 173);
    padding: 0;
    list-style: none;
    display: flex;
  }
  header li {
    padding: 10px 20px;
    position: relative;
  }
  header a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    transition: color 0.3s ease-in-out;
  }
  header li:hover a {
    color: #333;
  }
  header li::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 3px;
    background-color: transparent;
    transition: background-color 0.3s ease-in-out;
  }
  header li:hover::before {
    background-color: #4b8bf9;
  }
  header li:hover {
    background-color: #005f73;
    border-radius: 5px;
  }
  header img {
    padding-left: 38%;
    width: 180px;
    height: 80px;
  }
  .cart {
    margin: 50px 0;
    text-align: center;
  }
  .cart h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .cart p {
    font-size: 18px;
    color: #777;
  }
  </style>
  