<template>
  <div class="header">
            <router-link to="/"><a href="#" class="logo">
              <img src="img/image.png/image.png" alt="Logo">
          </a></router-link>
          <nav>
              <router-link to="/">NAM</router-link>
          </nav>
      </div>
      <div class="filter-container"></div>
      <h2>Lọc sản phẩm</h2>
      <div class="filter">
          <label for="price-range">Khoảng giá:</label>
          <select id="price-range" v-model="selectedPriceRange">
              <option value="all">Tất cả</option>
              <option value="0-500">0 - 500,000 VND</option>
              <option value="500-1000">500,000 - 1,000,000 VND</option>
              <option value="1000+">Trên 1,000,000 VND</option>
          </select>
      </div>
      <div class="filter">
          <label for="color">Màu sắc:</label>
          <select id="color" v-model="selectedColor">
              <option value="all">Tất cả</option>
              <option value="đỏ">Đỏ</option>
              <option value="Xanh">Xanh</option>
              <option value="đen">Đen</option>
              <option value="trắng">Trắng</option>
              <option value="xám">xám</option>
          </select>
      </div>
      <div class="product-container">
          <div class="product" v-for="product in filteredProducts" :key="product.id">
              <img :src="product.image" :alt="product.title" class="product-image">
              <h2 class="product-title">{{ product.title }}</h2>
              <!-- <p class="product-description">{{ product.description }}</p> -->
              <p class="product-price">{{ product.price }} VND</p>
              <div class="product-rating">{{ product.rating }}</div>
              <button class="buy-now" @click="buyNow(product)">Mua <i class="fas fa-shopping-cart"></i></button>
              <button class="add-to-cart" @click="addToCart(product)">Thêm <i class="fas fa-shopping-cart"></i></button>
          </div>
      </div>
    <!-- cái này chuyển từ app.vue qua đây -->
</template>

<script setup>
import { ref, computed } from 'vue';

// Các trạng thái và dữ liệu
  const selectedPriceRange = ref('all');
  const selectedColor = ref('all');
  const products = ref([
      { id: 101,image: './NAM/66d0bff0c7875b58eb83bd16f768072e.jpg',title: 'áo Polo Nam',price: 550000, rating: '⭐⭐⭐⭐☆',color: 'xám' },
      { id: 202,image: './NAM/92-d4e94057-f245-4f87-9939-931bfac0d235.webp',title: 'Tên sản phẩm 1',  price: 560000, rating: '⭐⭐⭐⭐☆',color: 'Xanh' },
      { id: 303,image: './NAM/130.webp',title: 'giày thể thao',  price: 620000, rating: '⭐⭐⭐⭐☆',color: 'trắng' },
      { id: 404,image: './NAM/5886def83d53b3c1320c9e38c6524aad.jpg',title: 'Quần thể thao Nam', price: 3000000, rating: '⭐⭐⭐⭐☆',color: 'xám' },
      { id: 505,image: './NAM/418998d5a718ea1af68d246d575fb820.giay-nam-cong-so-da-bo-tre-trung-M213.webp',title: 'giày nam màu đen', price: 700000, rating: '⭐⭐⭐⭐☆',color: 'đen' },
      { id: 606,image: './NAM/20191009_8AxO3wXlcFekyXGAAbFli4ms.jpg',title: 'Quần thể thao đen xám', price: 300000, rating: '⭐⭐⭐⭐☆',color: 'đen' },
      { id: 707,image: './NAM/afdt047-1l__2__077ae5567d054670a1610a4cbc05dc1c.webp',title: 'Áo phao ấm Nam & nữ', price: 650000, rating: '⭐⭐⭐⭐☆',color: 'đen' },
      { id: 808,image: './NAM/ao-thun-polo-mau-thach-3.jpg',title: 'áo vải nam chất lượng cao', price: 400000, rating: '⭐⭐⭐⭐☆',color: 'Xanh' },
      { id: 909,image: './NAM/Ao-under-armour-den.jpg',title: 'áo under màu đen',  price: 510000, rating: '⭐⭐⭐⭐☆',color: 'đen' },
      { id: 107,image: './NAM/dscf8576_e3e3da7ef08c4ed78205d2ce20aca0f4_master.jpg',title: 'Tất nam & nữ',  price: 80000, rating: '⭐⭐⭐⭐☆',color: 'xám' }
  ]);
  const cart = ref(JSON.parse(localStorage.getItem('cart')) || []);

  // Tính toán sản phẩm đã lọc
  const filteredProducts = computed(() => {
  return products.value.filter(product => {
      const priceFilter = selectedPriceRange.value === 'all' || 
      (selectedPriceRange.value === '0-500' && product.price <= 500000) ||
      (selectedPriceRange.value === '500-1000' && product.price > 500000 && product.price <= 1000000) ||
      (selectedPriceRange.value === '1000+' && product.price > 1000000);
      
      const colorFilter = selectedColor.value === 'all' || product.color.toLowerCase() === selectedColor.value.toLowerCase();
      
      return priceFilter && colorFilter;
  });
  });

  // Hàm thêm vào giỏ hàng
  const addToCart = (product) => {
  const cartProduct = cart.value.find(item => item.id === product.id);
  if (cartProduct) {
      cartProduct.quantity++; // Tăng số lượng nếu sản phẩm đã có
  } else {
      cart.value.push({ ...product, quantity: 1 }); // Thêm mới sản phẩm vào giỏ hàng
  }

  alert(`Bạn đã thêm ${product.title} với giá ${product.price.toLocaleString()} VND`);

  // Cập nhật giỏ hàng trong localStorage
  localStorage.setItem('cart', JSON.stringify(cart.value));
  };

  // Hàm xử lý mua hàng
  const buyNow = (product) => {
  alert(`Bạn đã mua ${product.title} với giá ${product.price.toLocaleString()} VND`);

  const cartProduct = cart.value.find(item => item.id === product.id);
  if (cartProduct) {
      cartProduct.quantity++;
  } else {
      cart.value.push({ ...product, quantity: 1 });
  }

  // Cập nhật giỏ hàng
  localStorage.setItem('cart', JSON.stringify(cart.value));
  };

  // Hàm kiểm tra tổng số sản phẩm trong giỏ hàng
  const cartItemCount = () => {
  return cart.value.reduce((total, product) => total + product.quantity, 0);
  };
</script>

