import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue' //trang chủ
import CartView from '../views/CartView.vue' // Import component giỏ
import NuView from '../views/NuView.vue'   // Import component NỮ
import NamView from '../views/NamView.vue'   // Import component Nam

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/cart',
    name: 'cart',
    component: CartView, // Sử dụng NamView
  },
  {
    path: '/nu',
    name: 'nu',
    component: NuView, // Sử dụng NuView
  },
  {
    path: '/nam',
    name: 'nam',
    component: NamView, // Sử dụng NuView
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
